












































import Vue from "vue";
import { Component, Ref, Prop } from "vue-property-decorator";
import Layout from "@/components/Layout.vue";
import axios from "axios";
// import { MessageBox } from "mint-ui";

@Component({ components: { Layout } })
export default class OutcomeSetting extends Vue {
  @Prop({ default: "out" }) readonly type: "in" | "out" | undefined;
  costTags: any[] = [];

  updateTextVi = [...Array(this.costTags.length).fill(false)];
  modalVi = false;
  updatedTags = [];
  valueToUpdate = "";
  modifiedIndex = 0;
  delMsgVi = false;
  isAdd = false;
  @Ref() modalInputEl!: HTMLInputElement;
  mounted() {
    // axios.post("", {}).then(() => {
    //   //
    // });
    this.costTags =
      this.type === "in"
        ? JSON.parse(JSON.stringify(this.$store.state.incomeTags))
        : JSON.parse(JSON.stringify(this.$store.state.costTags));
  }
  handleBack() {
    this.$router.go(-1);
  }
  handleSave() {
    console.log(this.costTags);
    let reqData = {};
    if (this.type === "in") {
      reqData = {
        userId: this.$store.state.userId,
        incomeTag: this.costTags,
      };
    } else {
      reqData = {
        userId: this.$store.state.userId,
        expenditureTag: this.costTags,
      };
    }
    axios
      .post("http://localhost:3000/api/account/update/settings", { ...reqData })
      .then(() => {
        //
      });
  }
  addNew() {
    //
    this.modalVi = true;
    this.isAdd = true;
  }

  updateTag(index: number) {
    console.log(index);
    this.updateTextVi[index] = !this.updateTextVi[index];
    console.log(this.updateTextVi);
  }
  modifyTag(index: number) {
    this.valueToUpdate = this.costTags[index].label;
    this.modalVi = true;
    this.modifiedIndex = index;
    this.$nextTick(() => {
      (this.$refs.modalInputEl as any).focus();
    });
  }

  delTag(index: number) {
    //
    this.modalVi = true;
    this.delMsgVi = true;
    this.modifiedIndex = index;
  }
  cancelModal() {
    this.modalVi = false;
    this.delMsgVi = false;
    this.isAdd = false;
    this.valueToUpdate = "";
  }
  confirmModal() {
    if (this.delMsgVi === true) {
      this.costTags.splice(this.modifiedIndex, 1);
    } else if (this.isAdd === true) {
      this.costTags.push({
        key: new Date().getTime(),
        label: this.valueToUpdate,
      });
    } else {
      this.costTags[this.modifiedIndex].label = this.valueToUpdate;
    }
    this.modalVi = false;
    this.delMsgVi = false;
    this.valueToUpdate = "";
    this.isAdd = false;
  }
}
